const findAll = async (req, res) => {
  try {
    let { query } = req;
    let { offset, limit, page } = query;
    if (offset || limit || page) {
      delete query.offset;
      delete query.limit;
      delete query.page;
      offset = offset ? Number(offset) : null;
      limit = limit ? Number(limit) : null;
      if (page) {
        offset = page ? Number(page) : null;
      }
    }
    const where = query ? query : {};
    const resultRes = await KnowledgeTest.findAll({
      limit,
      offset,
      where,
      include,
      order: [
        ["createdAt", "DESC"],
        ["id", "DESC"],
      ],
    });

    if (!resultRes) {
      res.status(200).send({
        response_status: "ERROR",
        message: err.message || "Some error occurred while finding.",
      });
      return;
    }
    res.status(200).send({
      response_status: "SUCCESS",
      data: resultRes,
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while finding.",
    });
  }
};

const findOne = async (req, res) => {
  try {
    const { id } = req.params;
    const data = await KnowledgeTest.findByPk(id, { include });
    // console.log("data", data);

    if (!data) {
      res.send({
        response_status: "ERROR",
        message: "Some error occurred while finding.",
      });
      return;
    }

    res.send({
      response_status: "SUCCESS",
      data: data,
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while finding.",
    });
  }
};

const update = async (req, res) => {
  try {
    const { id } = req.params;

    const result = await KnowledgeTest.update(req.body, {
      where: { id },
    });

    console.log("result", result);

    if (result != 1) {
      res.status(200).send({
        response_status: "ERROR",
        message: `(ID: ${id}) was not found.`,
      });
      return;
    }

    res.send({
      response_status: "SUCCESS",
      message: "Updated successfully",
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while updating.",
    });
  }
};

const remove = async (req, res) => {
  try {
    const { id } = req.params;
    const result = await KnowledgeTest.destroy({
      where: { id },
    });

    if (result !== 1) {
      res.status(200).send({
        response_status: "ERROR",
        message: `Log (ID: ${id}) was not found.`,
      });
      return;
    }

    res.send({
      response_status: "SUCCESS",
      message: "Deleted successfully",
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while deleting.",
    });
  }
};

export default {
  create,
  findAll,
  findOne,
  update,
  remove,
};
